.c-loading {
  width: 100%;
  height: 100vh;
  display: block;
  opacity: 1;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 99999;

  .type-ios & {
    height: 100svh;
  }

  &__main, &__bg {
    position: absolute;
    top: 0;
    left: 0;
  }

  &__main {
    z-index: 2;
    margin-top: calc(-40 / 390 * 100vw);
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    @include tab {
      margin-top: -40px;
    }

    .type-ios & {
      height: 100svh;
    }
  }

  &__bg {
    z-index: 0;
    width: 100%;
    height: 100vh;
    opacity: 0;
    position: relative;

    .type-ios & {
      height: 100svh;

      img {
        height: 100svh;
      }
    }

    .is-active & {
      animation: fadeIn 1.5s forwards;
    }
    @include tab {
      &::after {
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        z-index: 1;
        width: 100%;
        height: 100%;
        background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAIAAAACCAYAAABytg0kAAAAAXNSR0IArs4c6QAAABRJREFUGFdjZGBgeMzAwCDLyAAFAA25AQMnXzv+AAAAAElFTkSuQmCC) repeat top left / 3px;
      }
    }

    img {
      height: 100vh;
      object-fit: cover;
    }
  }

  .loading-txt-kt, .loading-txt-name, .loading-txt-sub, .loading-circle {
    opacity: 0;
  }
  .loading-txt-kt {
    width: calc(95 / 390 * 100vw);
    @include tab {
      width: 95px;
    }
  }
  &.is-active .loading-txt-kt {
    animation: fadeIn 1s 2.5s forwards;
  }
  .loading-txt-name {
    margin-top: calc(16 / 390 * 100vw);
    width: calc(198 / 390 * 100vw);
    @include tab {
      margin-top: 16px;
      width: 198px;
    }
  }
  &.is-active .loading-txt-name {
    animation: fadeIn 1s 1.5s forwards, vibration .5s 2.5s 6;
  }
  .loading-txt-sub {
    margin-top: calc(30 / 390 * 100vw);
    font-size: $fontSize11;
    font-weight: bold;
    @include lh(11, 17);
    text-align: center;
    color: $whiteColor;
    @include tab {
      margin-top: 30px;
    }
  }
  &.is-active .loading-txt-sub {
    animation: fadeIn 1s 2.5s forwards;
  }
  .loading-circle {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
    margin: auto;
    width: calc(222 / 390 * 100vw);
    height: calc(222 / 390 * 100vw);
    transform: rotate(-90deg);
    @include tab {
      width: 222px;
      height: 222px;
    }

    &__inner, &__rale {
      fill: transparent;
      stroke-width: 1;
    }

    &__inner {
      stroke: $grayColor1;
    }
  }
  &.is-active .loading-circle {
    animation: fadeIn 1s 3.5s forwards;
    &__rale {
      stroke: $mainColor1;
      animation: circle 4s 3.5s forwards;
    }
  }
}

body:not(.is-loading) .c-loading {
  opacity: 0;
  z-index: -1;
}

// css遅延読み込みによる表示崩れの映り込み回避
.l-container {
  display: none;
}