/* --------------------------------------------------------------------------

    Base

   -------------------------------------------------------------------------- */
* {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  width: 100%;
  color: $baseFontColor1;
  font-size: clampFs(12, 14, 16, 768);
  font-family: "游ゴシック", "游ゴシック Medium", "游ゴシック体", YuGothic, "Yu Gothic Medium", "Yu Gothic", sans-serif;
  word-break: break-all;
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
  position: relative;

  &.is-overlay, &.is-loading {
    overflow-y: hidden;
  }

  .type-ios &.is-overlay, .type-ios &.is-loading {
    position: fixed;
  }
}

header, footer, nav, section, article, aside, figure picture {
  display: block;
}

ul, ol {
  padding: 0;
  list-style: none;
}

em {
  font-weight: bold;
  font-style: normal;
}

// sup {
//   position: relative;
//   top: -.6em;
//   font-size: 70%;
// }

// sub {
//   position: relative;
//   bottom: 0;
//   font-size: 70%;
// }

a {
  color: $baseFontColor1;
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

a img {
  border-width: 0;
}

.is-loading {
  opacity: 0;
  animation: fadeIn 1s 1s forwards;

  .l-footer, .c-sec-uf, .c-sec-ph, .c-sec-recruit {
    display: none;
  }
}

/* Animation
   ========================================================================== */
.js-ani {
  opacity: 0;
}
.o-ani-fadeup {
  animation: fadeUp 1s ease-in forwards;
}
.o-ani-fadein {
  animation: fadeIn 1s ease-in forwards;
}
.o-ani-zoomout {
  animation: zoomOut2 2s 1s ease-in forwards;
}
@keyframes typing {
  0% {
    width: 0;
    opacity: 0;
  }
  100% {
    width: 100%;
    opacity: 1;
  }
}
@keyframes zoomOut {
  0% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes zoomOut2 {
  0% {
    transform: scale(1.4);
    opacity: 0;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}
@keyframes scroll {
  0% {
    height: 0;
    top: 0;
    opacity: 0;
  }
  30% {
    height: 100%;
    opacity: 1;
  }
  100% {
    height: 0;
    top: 40px;
    opacity: 0;
  }
}
@keyframes circle {
  0% {
    stroke-dasharray: 0 1000;
  }
  100% {
    stroke-dasharray: 1000 1000;
  }
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes slideOut {
  0% {
    opacity: 1;
    transform: translateX(0);
  }
  100% {
    opacity: 0;
    transform: translateX(-100%);
  }
}
@keyframes flash {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
}
@keyframes fadeRight {
  0% {
    opacity: 0;
    transform: translateX(-40px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes fadeUp {
  0% {
    opacity: 0;
    transform: translateY(40px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes w100 {
  0% {
    opacity: 0;
    width: 0;
  }
  1% {
    opacity: .9;
    width: 10%;
  }
  100% {
    opacity: 1;
    width: 100%;
  }
}
@keyframes gnavFadeIn {
  0% {
    display: none;
    opacity: 0;
  }
  1% {
    display: block;
    opacity: 0;
  }
  100% {
    display: block;
    opacity: 1;
  }
}
@keyframes vibration {
  0% {
    transform: translateY(0);
  }

  25% {
    transform: translateY(.1em);
  }

  50% {
    transform: translateY(0);
  }

  75% {
    transform: translateY(.1em);
  }

  100% {
    transform: translateY(0);
  }
}